.book {
    display: inline-block;
    width: 190px;
    height: 280px;
    margin: 10px;
    text-align: left;
    cursor: pointer;
    position: relative;
  }
  
  .book-img {
    width: 190px;
    height: 280px;
    border: 4px solid #000;
    border-radius: 4px;
    -o-object-fit: cover;
       object-fit: cover;
    position: absolute;
  }
  
  .book-text {
    background-color: #000;
    color: #fff;
    opacity: 0.75;
    width: 186px;
    margin-left: 3px;
    text-align: center;
    position: absolute;
    bottom: 0;
    padding: 10px;
  }


